.loader-wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: black;
    display: grid;
    place-items: center;
}

.loader {
    border-left: 2px solid white;
    position: absolute;
    left: 50%;
    margin-left: -3px;
    height: 100%;
    animation: slideDown 2s ease-in-out;
}

@keyframes slideDown {
    0% {
        transform: translate(0, -100%);
    }
    100% {
        transform: translate(0, 0%);;
    }
}